/* Global theme (aligned with Navbar) */
:root {
  --brand-900: #191e25; /* navbar background */
  --accent-500: #649a8b; /* navbar search button */
  --accent-600: #4e7d71;

  --page-bg: #f5f5f5;
  --surface-1: #ffffff;
  --surface-2: #f9f9f9;
  --surface-muted: #f2f2f2;

  --text-1: #191e25;
  --text-2: #6e6e73;
  --text-invert: #ffffff;

  --border-1: rgba(25, 30, 37, 0.10);
  --shadow-1: 0 6px 18px rgba(0, 0, 0, 0.06);
  --shadow-2: 0 14px 36px rgba(0, 0, 0, 0.12);
  --radius-1: 12px;
  --radius-2: 16px;
}

body {
  margin: 0;
  font-family: "Segoe UI", system-ui, -apple-system, BlinkMacSystemFont, "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background: var(--page-bg);
  color: var(--text-1);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.banner-container {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.banner {
  width: 100%;
  height: 300px;       /* ÂèØ‰ª•ÊîπÊàê 400px ÁúãËµ∑‰æÜÊõ¥ÂÉèËù¶ÁöÆ */
